import pageHeader from '../common/page-header'
import goodsByCategoryListGoods from '../goods-by-category/common/goods-by-category-list-goods'
export default {
  components: {
    goodsByCategoryListGoods,
    pageHeader,
  },
  methods: {

  },
  created () {
    this.request(this.api.ShopInfo,{shopId:1}).then(res => {
      if (res.code == 200) {
        this.ShopInfo = res.data
      }
    });
    this.request(this.api.listShopLatestGoods,{shopId:1}).then(res => {
      if (res.code == 200) {
        this.listShopLatestGoods = res.data
      }
    });
  },
  data(){
    return {
      ShopInfo: [],
      listShopLatestGoods: [],
    }
  }
}



